.modal {
  position: fixed;
  display: flex;
  align-items: baseline;
  justify-content: center;
  top: 20px;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  overflow-y: auto;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 768px) {
    top: 226px;
  }
  @media screen and (min-width: 1280px) {
    top: 170px;
  }
}

.modal .overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $main-black;
  opacity: 0.7;
  z-index: 0;
}

.modal-window {
  opacity: 0;
  position: relative;
  z-index: 2;
  width: 280px;
  max-height: 940px;
  padding: 48px 20px 40px 20px;
  background-color: $main-white;
  border-radius: 5px;
  box-shadow: 0px 1px 3px rgba(255, 255, 255, 0.12),
    0px 1px 1px rgba(255, 255, 255, 0.14), 0px 2px 1px rgba(255, 255, 255, 0.2);
  @media screen and (min-width: 768px) {
    padding: 40px 0px 0px 36px;
    width: 704px;
    height: 552px;
  }
  @media screen and (min-width: 1280px) {
    width: 806px;
    height: 558px;
  }
  main.dark-theme & {
    background-color: rgb(0, 0, 0);
  }
}

.modal-img {
  width: 240px;
  height: 357px;
  border-radius: 5px;
  @media screen and (min-width: 768px) {
    width: 264px;
    height: 374px;
  }
  @media screen and (min-width: 1280px) {
    width: 375px;
    height: 478px;
  }
}

.btn-close {
  position: absolute;

  top: 8px;
  right: 8px;
  width: 30px;
  height: 30px;
  margin-left: auto;

  background-color: transparent;
  border: none;
  main.dark-theme & {
    border-color: #fff;
    border: 1px solid #fff;
    border-radius: 50%;
  }
}
.btn-close__svg {
  position: relative;
  right: 7px;
  bottom: 2px;
  width: 30px;
  height: 30px;
  stroke: currentColor;
  fill: currentColor;
  main.dark-theme & {
    color: #fff;
  }
}

.modal-info {
  @media screen and (min-width: 768px) {
    margin-left: 63px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: 16px;
  }
}

.clearing-modal {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.mobal__title {
  margin-top: 20px;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: $main-black;
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 35px;
  }
  main.dark-theme & {
    color: #fff;
  }
}
.mobal__list--description,
.mobal__list--info {
  margin-top: 20px;

  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
   main.dark-theme & {
    color: #fff;
  }
}

.mobal__list--description {
  color: $information-modal-text;
  
}

.helper-in-modal {
  display: flex;
  gap: 41px;
}

.mobal-list__item {
  display: flex;
}
.mobal-list__item:not(:first-child) {
  padding-top: 8;
}
.mobal-list__item:first-child {
  color: $information-modal-text;
}

.module__text-vote,
.module__text-votes,
.module__text-popularity {
  line-height: 16px;

}
.module__text-vote {
  padding: 1px 10px;
  border-radius: 5px;
  background-color: $main-orange;
  color: $main-white;
}
.module__text-votes {
  padding: 1px 4px;
  border-radius: 5px;
  background-color: $main-gray;
  color: $main-black;
}

.mobal__title--about {
  margin-top: 20px;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  color: $main-black;
  main.dark-theme & {
    color: #fff;
  }
}

.mobal__title--about-text {
  height: 160px;
  overflow-y: scroll;
  margin-top: 8px;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
  color: $main-black;
  @media screen and (min-width: 768px) {
    width: 264px;
    height: 236px;
  }
  @media screen and (min-width: 1280px) {
    width: 391px;
    height: 160px;
  }
  main.dark-theme & {
    color: #fff;
  }
}

.modal-btn__list {
  margin-top: 20px;
  display: flex;
  gap: 20px;
}
.mobal-item__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-align: left;
  main.dark-theme & {
    color: #fff;
  }
}
.modal-btn__watched,
.modal-btn__queue {
  border-radius: 5px;
  border: 1px solid $main-black;
  width: 110px;
  height: 44px;
  background-color: transparent;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;

  color: $main-black;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: $main-orange;
    color: $main-white;
    border: 1px solid $main-orange;
  }
  @media screen and (min-width: 768px) {
    padding-left: 9px;
    width: 125px;
    height: 44px;
  }
  @media screen and (min-width: 1280px) {
    width: 136px;
    height: 44px;
  }
  main.dark-theme & {
    color: #fff;
    border: 1px solid #fff;
  }
}
